@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;600;700&display=swap);
.Button{display:inline-block;height:34px;min-height:34px;box-sizing:border-box;width:100px;font-size:var(--fs-md);border-radius:var(--button-br);color:var(--button-color);background-color:var(--button-primary-bg);cursor:pointer}.Button__container{display:flex;align-items:center;justify-content:center;width:100%;height:100%;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.Button:hover{background-color:var(--button-hover-bg)}.Button--disabled,.Button--disabled:hover{color:var(--button-disabled-color);background-color:var(--button-disabled-bg);cursor:wait}

.Header{background-color:var(--white);position:fixed;top:0;right:0;width:100%;z-index:9999;box-shadow:0 2px 4px 0 rgba(0,0,0,0.04)}.Header__container{height:60px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;grid-gap:0.2rem;gap:0.2rem}.Header__logo button{border:none;outline:none;background-color:transparent;display:flex;align-items:center;cursor:pointer}.Header__nav{display:flex;grid-gap:0.2rem;gap:0.2rem}@media (min-width: 768px){.Header__nav{grid-gap:1rem;gap:1rem}}

.Container{padding:0;width:100%;max-width:1170px;min-width:360px;margin:0 auto}

.Wrapper{width:100%;padding:0 16px 0 16px;box-sizing:border-box}@media (min-width: 768px){.Wrapper{padding:0 32px 0 32px}}@media (min-width: 1024px){.Wrapper{padding:0 60px 0 60px}}@media (min-width: 1170px){.Wrapper{padding:0}}

.Promo__poster{min-height:500px;width:100%;height:100vh;box-sizing:border-box;background-repeat:no-repeat;background-size:cover;background-position:center;color:var(--white);background-image:url(../../static/media/promo-360.f09745ec.png);font-weight:300;text-align:center;display:flex;flex-direction:column;align-items:center;justify-content:center}.Promo h1{margin:40px auto 0;font-size:var(--fs-h1);font-weight:var(--fw-normal);line-height:var(--lh-h1);max-width:350px;text-align:center}.Promo p{margin:0 auto;padding-top:21px;max-width:328px;font-size:var(--fs-md);font-weight:var(--fw-normal);line-height:var(--lh-md);padding-bottom:32px}@media (min-width: 768px){.Promo__poster{background-image:url(../../static/media/promo-768.78baa64c.png)}.Promo h1{max-width:450px}.Promo p{max-width:381px;padding-bottom:36px}}@media (min-width: 1024px){.Promo__poster{background-image:url(../../static/media/promo-1024.5fff95b2.png)}}

.Card{height:254px;background-color:var(--white);color:var(--black);border-radius:var(--card-br);padding:20px;box-sizing:border-box;font-size:var(--fs-md);line-height:var(--lh-md);overflow:hidden;text-align:center}.Card__container{width:100%;display:flex;flex-direction:column;align-items:center}.Card__photo{width:70px;height:70px;border-radius:70px;margin-bottom:20px}.Card__name{margin-bottom:20px}.Card__email{position:relative;cursor:pointer}.Card__email--hover{overflow:visible;position:absolute;bottom:10px;left:100px;z-index:50;background-color:red;visibility:hidden}.Card__email:hover .Card__email--hover{visibility:visible}.Card__email,.Card__name{overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow-wrap:break-word}

.Payload{-webkit-animation:slideIn 0.3s ease-in;animation:slideIn 0.3s ease-in;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes slideIn{from{-webkit-transform:translateX(100%);transform:translateX(100%)}to{-webkit-transform:translateX(0%);transform:translateX(0%)}}@keyframes slideIn{from{-webkit-transform:translateX(100%);transform:translateX(100%)}to{-webkit-transform:translateX(0%);transform:translateX(0%)}}

.List{display:grid;grid-template-columns:repeat(1, 1fr);grid-gap:20px;gap:20px}@media (min-width: 768px){.List{grid-template-columns:repeat(2, 1fr);grid-gap:16px;gap:16px}}@media (min-width: 1024px){.List{grid-template-columns:repeat(3, 1fr);grid-gap:29px;gap:29px}}

.Loader{display:flex;width:50px;justify-content:center;align-items:center}.Loader__content{border-radius:50%;width:48px;height:48px;border:4px solid var(--blue);border-left-color:transparent;-webkit-animation:load8 1.2s infinite linear;animation:load8 1.2s infinite linear}@-webkit-keyframes load8{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes load8{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.ArticleGet{position:relative;min-height:100vh}.ArticleGet__anchor-head{opacity:0.3;position:absolute;height:100px;width:100px;top:0;z-index:5000}.ArticleGet__anchor-bottom{opacity:0.3;position:absolute;height:100px;width:100px;bottom:calc(100vh - 100px);z-index:5000}.ArticleGet__content{width:100%}.ArticleGet__title{font-size:var(--fs-h1);font-weight:var(--fw-normal);color:var(--black);text-align:center;margin:0 auto;padding:140px 0 50px}@media (max-width: 768px){.ArticleGet__title{max-width:320px}}.ArticleGet__button-container{height:84px;display:flex;flex-direction:column;align-items:center;justify-content:end;padding-bottom:20px}.ArticleGet__button-container--hidden{height:20px;visibility:hidden}@media (min-width: 1024px){.ArticleGet{display:block}}@media (max-height: 1025px){.ArticleGet{min-height:100vh}.ArticleGet--first-load{display:flex;align-items:center;justify-content:center}}

.Input{width:100%;max-width:380px;display:block;position:relative;padding:0;box-sizing:border-box;cursor:pointer}.Input__label{margin:0;box-sizing:border-box;font-size:var(--fs-sm);color:var(--field-color-placeholder);background-color:var(--bg-color);position:absolute;top:-7px;left:11px;padding:0 4px;cursor:pointer}.Input__input{width:100%;border:1px solid var(--field-border-enabled);height:54px;padding:14px 16px;border-radius:var(--field-radii);box-sizing:border-box;background-color:transparent;color:var(--black);outline:none}.Input__input:focus,.Input__input:active{border:1px solid var(--field-border-enabled)}.Input__input--error{border:2px solid var(--field-color-border-error)}.Input__error-container{position:absolute;z-index:5000;top:58px}.Input__error,.Input__helpers{margin:0;box-sizing:border-box;font-size:var(--fs-sm);line-height:var(--lh-sm);color:var(--error-color);background-color:transparent;padding:0 16px;cursor:pointer;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow-wrap:break-word}.Input__helpers{color:var(--field-color-placeholder)}

.Radio{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:25px;display:flex;align-items:center;font-size:var(--fs-md);font-weight:var(--fw-normal);line-height:var(--lh-md);color:var(--black)}.Radio input{display:none}.Radio span{margin-left:12px}

.Select{width:100%;max-width:380px;position:relative}.Select__title{margin-bottom:11px}.Select__radio{margin-bottom:7px}.Select__errors-container{position:absolute;z-index:5000;top:calc(100%);left:14px}.Select__error{margin:0;box-sizing:border-box;font-size:var(--fs-sm);line-height:var(--lh-sm);color:var(--error-color);background-color:transparent;padding:0 16px;cursor:pointer;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow-wrap:break-word}

.InputFile{display:block;width:100%;max-width:380px;min-height:54px;border-radius:var(--field-radii);border:1px solid var(--field-border-enabled);position:relative;padding-top:14px;box-sizing:border-box}.InputFile__button{position:absolute;top:-1px;left:-1px;display:inline-block;height:54px;width:83px;text-align:center;border-radius:var(--field-radii) 0 0 var(--field-radii);border:1px solid var(--black);padding-top:14px}.InputFile__value{padding-left:99px;padding-right:16px;color:var(--black);overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow-wrap:break-word}.InputFile__value--empty{color:var(--field-color-placeholder)}.InputFile__error-container{position:absolute;z-index:5000;top:62px}.InputFile__error{margin:0;box-sizing:border-box;font-size:var(--fs-sm);line-height:var(--lh-sm);color:var(--error-color);background-color:transparent;padding:0 16px;cursor:pointer;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow-wrap:break-word}.InputFile--error{border:2px solid var(--field-color-border-error)}.InputFile--error>.InputFile__button{top:-2px;left:-2px;border:2px solid var(--field-color-border-error)}

.Form{height:655px;display:flex;flex-direction:column;align-items:center;justify-content:center;position:relative;padding-bottom:80px}.Form__title-success{font-size:var(--fs-h1);font-weight:var(--fw-normal);color:var(--black);text-align:center;margin:0 auto}@media (max-width: 768px){.Form__title-success{max-width:320px}}.Form__server-message{color:var(--error-color)}.Form__input{margin-bottom:50px}.Form__input-phone{margin-bottom:43px}

.ArticlePost{position:relative;display:flex;align-items:center;justify-content:center;min-height:100vh}.ArticlePost__anchor{position:absolute;top:0}.ArticlePost__content{padding-bottom:20px}.ArticlePost__title{font-size:var(--fs-h1);font-weight:var(--fw-normal);color:var(--black);text-align:center;margin:0 auto;padding-top:100px;margin-bottom:50px}@media (max-width: 768px){.ArticlePost__title{max-width:320px}}@media (min-width: 1024px){.ArticlePost{height:auto}}


/* @import url('./reset.css');  */
/* drop styles button */

:root {
  /* Typography */
  --family: 'Nunito', sans-serif;
  --fs-sm: 12px;
  --fs-md: 16px;
  --fs-h1: 40px;

  --fw-normal: 400;
  --lh-sm: 14px;
  --lh-md: 26px;
  --lh-h1: 40px;

  /* Other */
  --yellow: #F4E041;
  --blue: #00BDD3;
  --light-grey: #F8F8F8;
  --bg-color: #E5E5E5;
  --white: #FFFFFF;
  --black: rgba(0, 0, 0, 0.87);
  --error-color: #CB3D40;
  
  /* UI */
  --button-color: rgba(0, 0, 0, 0.87);
  --button-primary-bg: #F4E041;
  --button-hover-bg: #FFE302;
  --button-disabled-bg: #B4B4B4;
  --button-disabled-color: rgba(255, 255, 255, 0.87);
  --button-br: 80px;
  --button-radii: 4px;
  
  --field-border-enabled: #D0CFCF;
  --field-radii: 4px;
  --field-color-placeholder: #7E7E7E;
  --field-color-border-error: #CB3D40;

  --card-br: 10px;
}

* {
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
  font-family: var(--family);
  font-size: 16px;
  font-size: var(--fs-md);
  margin: 0;
  padding: 0;
}

body {
  background-color: #E5E5E5;
  background-color: var(--bg-color);
  min-width: 360px;
  overflow-x: hidden;
}

